.full{
    background-color: #1565c0;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    .authentication-main{
        width: 100%;
        align-items: center;
        .content-wrap-signin {
            align-items: center;
            .signin_quote{
                color: #ffcc05;
                text-transform: uppercase;
                font-size: 1.7em;
                margin-bottom: 30px;
                display: block;
            }
            .card{
                margin-bottom: 30px;
                border: 0;
                transition: all .3s ease;
                letter-spacing: .5px;
                border-radius: 8px;
                box-shadow: 1px 5px 24px 0 rgba(68,102,242,.05);
                .card-body{
                    .theme-form{
                        p{
                            white-space: nowrap;
                        }
                        .pc_link{
                            a{
                                justify-content: flex-start;
                                display: flex;
                                align-items: center;
                                color: #4466f2;
                                img{
                                    margin: 0px;
                                    width: 50px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}